import { _decorator, Component, Node } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('ReelController')
export class ReelController extends Component {

	

    start() {

    }

    update(deltaTime: number) {
        
    }


    trunReel() {
    	cc.tween(this.node)
            .to(0.1, { angle: -20 })
            .to(0.3, { angle: 20 })
            .to(0.3, { angle: -20 })
            .to(0.3, { angle: 20 })
            .to(0.3, { angle: -20 })
            .to(0.3, { angle: 20 })
            .to(0.3, { angle: -20 })
            .to(0.3, { angle: 20 })
            .to(0.3, { angle: -20 })
            .to(0.1, { angle: 0 })
            .start();
    }

}

